import { Component, OnInit } from '@angular/core';
import { DetailsComponentBase } from '../../../../../../shared/ui/objects-view/details-component-base';
import { IPricingPlanModel } from '../../../../../../shared/fleio-api/cloud/model/pricing-plan.model';
import { ConfigService } from '../../../../../../shared/config/config.service';
import { Observable, of } from 'rxjs';
import { NotificationService } from '../../../../../../shared/ui-api/notification.service';
import { PricingRulesApiService } from '../../../../../../shared/fleio-api/cloud/pricing-rule/pricing-rules-api.service';
import { map } from 'rxjs/operators';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-pricing-plan-details-overview',
  templateUrl: './pricing-plan-details-overview.component.html',
  styleUrls: ['./pricing-plan-details-overview.component.scss']
})
export class PricingPlanDetailsOverviewComponent extends DetailsComponentBase<IPricingPlanModel> implements OnInit  {
  displayedColumns: string[] = ['display_name', 'resource_name', 'price', 'actions'];
  constructor(private config: ConfigService, private notificationService: NotificationService,
              private pricingRulesApiService: PricingRulesApiService, private router: Router,
              private route: ActivatedRoute ) {
    super();
  }
  private deletePricingRule(id) {
    let dialogResult$: Observable<string>;
    dialogResult$ = this.notificationService.confirmDialog({
        title: 'Delete pricing rule?',
        message: 'Deleting the princing rule will apply to all calculated costs',
      });
    dialogResult$.subscribe(dialogResult => {
      if (dialogResult === 'yes') {
        this.pricingRulesApiService.delete(id).pipe(map(success => {
          if (success) {
            this.notificationService.showMessage('Successfully deleted price rule.');
            const queryParams = {};
            Object.assign(queryParams, this.route.snapshot.queryParams);
            this.router.navigate([],  {
              relativeTo: this.route,
              queryParams
            }).catch(() => {
              // TODO: handle navigation error
            });
          } else {
            this.notificationService.showMessage('Could not delete price rule.');
          }
        })).subscribe();
      }
    });
  }
  ngOnInit() {
  }

}
